/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1.1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --corpRed: hsl(4, 77%, 54%);
  --lightGrey: hsl(0, 0%, 83%);
  --midGrey: hsl(0, 0%, 41%);
  --darkGrey: hsl(0, 0%, 25%);
  --black: hsl(0, 0%, 9%);
  --white: hsl(0, 0%, 100%);
  --white80: hsla(0, 0%, 100%, 0.8);
  --textGrey: hsl(0, 0%, 445);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif; /* body font */
  --font-heading: "Roboto", Helvetica, Arial, sans-serif; /* heading font */
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
}

.container-lg {
  width: 80%;
  max-width: 87.5rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .container-lg {
    width: 100%;
    position: relative;
  }
}

.container {
  width: 80%;
  max-width: 75rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .container {
    width: 100%;
    position: relative;
    padding: 0 0.625rem 0 0.625rem;
  }
}

.row {
  padding: 0 0.625rem;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 75em) {
  .hide-for-desktop {
    display: none;
  }
}

/* Full Width
/* -------------------------------------*/
.fullWidth__blogs--wrapper {
  width: 60%;
  max-width: 75rem;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media (max-width: 63.9375em) {
  .fullWidth__blogs--wrapper {
    width: 100%;
    position: relative;
    flex-direction: row;
  }
}
@media (max-width: 39.9375em) {
  .fullWidth__blogs--wrapper {
    width: 100%;
    position: relative;
    flex-direction: column;
  }
}
.fullWidth__rowReverse {
  flex-direction: row-reverse;
}
.fullWidth__section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  overflow: hidden;
  position: relative;
  justify-content: flex-start;
  align-items: center;
}
.fullWidth__section--reverse {
  flex-direction: row-reverse;
}
@media (max-width: 39.9375em) {
  .fullWidth__section {
    display: block;
    height: 100%;
    position: relative;
    overflow: visible;
  }
}

.btn__contact a {
  padding: 0.5rem 1.25rem;
  background-color: var(--white);
  border: 0.0625rem solid var(--darkGrey);
  color: var(--darkGrey);
  border-radius: 0;
  margin-left: 1.875rem;
  cursor: pointer;
}
@media (max-width: 63.9375em) {
  .btn__contact a {
    margin-left: 0;
  }
}
.btn__contact a:hover {
  background-color: var(--corpRed);
  border: 0.0625rem solid var(--darkGrey);
  color: var(--white);
  transition: color 350ms ease-in-out;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-family: var(--font-heading);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header {
  background-color: var(--white80);
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 100%;
  z-index: 999;
}
@media (max-width: 39.9375em) {
  .header {
    height: 5.625rem;
  }
}
@media (min-width: 75em) {
  .header .hamburger {
    visibility: hidden;
  }
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  top: 5.625rem;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
}
@media (min-width: 40em) {
  .header .overlay {
    top: 6.875rem;
  }
}
@media (max-width: 39.9375em) {
  .header .overlay {
    position: absolute;
  }
}
.header__logo {
  height: 5.1875rem;
  padding-top: 0.625rem;
  display: block;
}
@media (max-width: 39.9375em) {
  .header__logo img {
    width: 100%;
  }
}
.header__toggle {
  margin-right: 1.25rem;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--corpRed);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}

.nav {
  height: auto;
  position: relative;
}
.nav__link {
  color: var(--darkGrey);
  text-transform: uppercase;
  text-decoration: none;
  font-family: var(--font-heading);
  font-size: 1.0625rem;
  font-weight: 500;
}
@media (max-width: 63.9375em) {
  .nav__link {
    margin-top: 1.25rem;
  }
}
.nav__link:hover, .nav__link:active {
  color: var(--corpRed);
}
@media (max-width: 39.9375em) {
  .nav__link {
    margin-top: 1.25rem;
  }
}
.nav__item {
  margin: 0 0 0 1.5rem;
}
@media (max-width: 63.9375em) {
  .nav__item {
    padding: 1.25rem 0;
    margin: 0;
  }
}
.nav__list {
  display: flex;
  margin: 0;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: auto;
  text-align: center;
  top: 5.625rem;
  z-index: 999;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  position: absolute;
  display: block;
  background-color: white;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translate(-50%);
  height: auto;
  text-align: center;
  top: 5.625rem;
  padding: 1.25rem 0 2.5rem 0;
  z-index: 999;
  margin: 0 auto;
  border-radius: 0 0 0.3125rem 0.3125rem;
}
@media (max-width: 63.9375em) {
  .nav__list--open {
    top: 3.75rem;
  }
}

.frontPage__hero {
  background-image: url(/img/index-handsIn_1920x1080.jpg);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  left: 50%;
  transform: translateX(-50%);
  position: relative;
  margin: 0;
  height: 100vh;
  overflow: hidden;
  clear: both;
}
@media (max-width: 63.9375em) {
  .frontPage__hero {
    background-position: 75% top;
    background-color: black;
  }
}
.frontPage__hero--headline {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  position: absolute;
  top: 40%;
  left: 20%;
  z-index: 900;
  width: 100%;
}
@media (max-width: 63.9375em) {
  .frontPage__hero--headline {
    left: 10%;
  }
}
.frontPage__hero--headline h1,
.frontPage__hero--headline h2 {
  color: var(--white);
  text-shadow: 0 0 0.4375rem var(--black);
}
.frontPage__hero--headline h1 {
  font-size: 2.5rem;
  margin-bottom: 1.875rem;
  width: 40%;
}
@media (max-width: 63.9375em) {
  .frontPage__hero--headline h1 {
    width: 80%;
  }
}
.frontPage__hero--headline h2 {
  font-size: 1.75rem;
  margin-bottom: 1.5625rem;
  width: 30%;
}
@media (max-width: 63.9375em) {
  .frontPage__hero--headline h2 {
    width: 80%;
  }
}